import store from "../store"
import { TOAST_DISMISS, TOAST_ERROR, TOAST_LOADING, TOAST_SUCCESS, TOAST_UPDATE } from "../utils"
import { CREATE_FEED, GET_COUNT, HOST_URL, ONBOARDING_CREATE_FEED, ONSITE_FEED } from "./api"
import HttpClient from "./http-client"
import { getUserInfo_afterChanges } from "./loginActions"
import { TAGSHOP_USER_HOME_DATA } from "./sceneActions"
import { togglePriceSection } from "./show_modal_Action"


export const CREATE_OB_FEED = (data, callBack, onLoading) => {
    try {
        new HttpClient().post(CREATE_FEED, data, { validateStatus: false }).then(res => {
            const { responseCode, responseMessage, responseData, postCount, } = res.data
            localStorage.setItem('selectedNetwork', null)
            if (responseCode == 200) {
                TOAST_SUCCESS(responseMessage)
                if (data.filter != 62 && responseData) GET_OB_POST_COUNT(responseData, data.networkId, callBack, onLoading)
            }
            else {
                TOAST_ERROR(responseMessage)
                onLoading(false)
            }
        }).catch(ex => {
            TOAST_ERROR("Some Error Occured!!")
            onLoading(false)
        })
    } catch (error) {
        onLoading(false)
    }
}


export const GET_OB_POST_COUNT = (feedId, networkId, callBack, onLoading) => {
    const obj = {
        wallId: localStorage.getItem('wallId'),
        feedId,
        networkId: networkId
    }
    const toastId = TOAST_LOADING()
    new HttpClient().post(GET_COUNT, obj, { validateStatus: false }).then(res => {
        const { responseCode, responseMessage, postCount } = res.data
        if (responseCode == 200) {
            TOAST_UPDATE(toastId, "success", `${postCount} ${postCount > 1 ? 'posts' : 'post'} added successfully`)
            onLoading(false)
            if(localStorage.getItem('shopifyLogin')) store.dispatch(getUserInfo_afterChanges())
            //store.dispatch(TAGSHOP_USER_HOME_DATA())
            callBack()
        }
        else {
            TOAST_UPDATE(toastId, "error", responseMessage);
            onLoading(false)
        }
    }).catch(error => {
        TOAST_DISMISS(toastId)
        onLoading(false)
    })
}

export const CREATE_OB_FEED_FB = (data, callBack, onLoading) => {
    try {
        new HttpClient().post(ONBOARDING_CREATE_FEED, data, { validateStatus: false }).then(res => {
            const { responseCode, responseMessage, response, postCount, } = res.data
            localStorage.setItem('selectedNetwork', null)

            if (responseCode == 200) {
                TOAST_SUCCESS(responseMessage)
                if (postCount != null && data.filter != 62) {
                    TOAST_SUCCESS(`${postCount} post added successfully`)
                }

                if (data.filter != 62 && response && response.length) {
                    GET_OB_POST_COUNT(response[0], data.networkId, callBack, onLoading)
                }
                if (data.filter == 62 && postCount && postCount.length) {
                    GET_OB_POST_COUNT(postCount[0], data.networkId, callBack, onLoading)
                }
            }
            else {
                TOAST_ERROR(responseMessage)
                onLoading(false)
            }
        }).catch(err => {
            TOAST_ERROR("Some Error Occured!!")
            onLoading(false)
        })
    } catch (error) {
        onLoading(false)
    }
}

export const CREATE_OB_ONSITE_FEED = (obj, callBack, onLoading) => {
    new HttpClient().post(ONSITE_FEED, obj, { validateStatus: false }).then(res => {
        const { responseCode, responseData, responseStatus } = res.data
        if (parseInt(responseCode) == 200) {
            TOAST_SUCCESS(responseData)
            callBack()
        }
        else TOAST_ERROR(responseData)
        if (responseStatus == "Upgrade") {
            setTimeout(() => {
                store.dispatch(togglePriceSection(true, 2))
            }, 1500);
        }
        onLoading(false)

    })
}