import React, { Component } from 'react'
import { HOST_URL } from '../../../../../../actions/api'
import { Image, Nav, InputGroup, Form, Card, Button, Tab, Alert } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../../utils';
import Swal from 'sweetalert2'
import withRouter from '../../../../../routes/WithRouter'
import Google from './FeedNetworks/Google'
import Facebook from './FeedNetworks/Facebook'
import Youtube from './FeedNetworks/Youtube'
import { CREATE_OB_FEED, CREATE_OB_FEED_FB, CREATE_OB_ONSITE_FEED } from '../../../../../../actions/onBoradingAction'
import { getInputIcon } from '../../../Feeds/AddFeed/utils';
import OnBoardingManualUpload from '../../../../../Layout/Main/Feeds/ManualUpload/OnBoardingManualUpload';
import ManualHead from '../../../Feeds/ManualUpload/ManualHead';
import ManualUploadWrap from '../../../Feeds/ManualUpload/ManualUploadWrap';


const SHOW_MORE_TAB = (networkId) => {
    const showTabNetwork = [18, 1, 3, 4]
    if (showTabNetwork.includes(networkId)) return true
    return false
}
class AddContent extends Component {

    state = { activeFilter: "", feedText: "", placeId: '', placeName: '', showError: false, channelId: '', filterName: "", isLoading: false, latLng: "", keyword: "", error: false, feedTextError: false, manualSelect:null }

    componentDidMount() {
        const { contentObject } = this.props
        if (contentObject.filters && contentObject.filters.length) this.setState({ activeFilter: contentObject.filters[0].filter, filterName: contentObject.filters[0].feedtype })
    }

    componentDidUpdate(prevProps, prevState) {
        const { contentObject } = this.props
        if (prevProps.contentObject.filters != contentObject.filters) {
            if (contentObject.filters && contentObject.filters.length) this.setState({ activeFilter: contentObject.filters[0].filter, filterName: contentObject.filters[0].feedtype })
        }
    }

    onMoreTab = () => {
        const { updateData, selectedNetworkId } = this.props
        Swal.fire({
            title: "You have to connect your Account to use more options",
            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "I'll do it later",
            cancelButtonText: "Proceed",
            cancelButtonColor:`var(--bs-success)`,
        }).then((result) => {
            if (result.isDismissed && result.dismiss == "cancel") {
                localStorage.setItem("selectedNetwork", selectedNetworkId)
                updateData({ SKIP: true })
                this.props.navigate(`/content/addfeed/${localStorage.getItem("wallId")}`)
            }
        });
    }

    onFieldTextChange = (text, id, placeName) => this.setState({ feedText: text, placeId: id, placeName: placeName, showError: false })

    searchText = (value) => { this.setState({ feedText: value, error: false }) }

    onLatlanChange = (value) => this.setState({ latLng: value })

    onChannelId = (value) => this.setState({ channelId: value })

    changeKeyword = (value) => this.setState({ keyword: value })

    onLoading = (value) => this.setState({ isLoading: value })

    onInputFeedText = (event) => this.setState({ feedText: event.target.value })

    onAddFeed = () => {
        const { activeFilter, feedText, channelId, placeId, placeName, latlng, keywords } = this.state
        const { selectedNetworkId, contentObject, onNext } = this.props
        this.onLoading(true)
        if (selectedNetworkId == 29) {
            const obj = {
                network: 29,
                filter: 2,
                moderation: 0,
                onsite_qr_code_btn_text: "Scan QR code or click here to submit reviews/rating",
                onsite_btn_color: "#000000",
                onsite_btn_txt_color: "#ffffff",
                onsite_link_font_btn: "Lato",
                onsite_css_font_btn: "Lato",
                onsite_font_varient_btn: "",
                btn_fontSize: 16,
                onsite_qr_code_status: 1,
                onsite_popup_title: "Submit your posts",
                onsite_popup_btn_txt: "Submit post",
                onsite_popup_sub_heading: "You can share your reviews/rating here",
                onsite_term_url: "https://taggbox.com/on-site-uploads-content-rights/",
                onsiteImgBack: "",
                onsite_logo: "",
                Onsite_logoImageValue: "",
                onsite_bg_color_wg: "#E8E8E8",
                onsiteBackImage: "",
                onsite_bg_image_wg: "",
                Onsite_backImageValue: "",
                onsite_succ_msg: "Thanks for the submission. Your post will live on the website after approval.",
                onsite_thank_txt_color: "#000000",
                onsite_link_font_msg: "Default",
                onsite_css_font_msg: "Default",
                onsite_font_varient_msg: "",
                onsite_thank_fontSize: 16,
                wallId: localStorage.getItem('wallId')
            }
            CREATE_OB_ONSITE_FEED(obj, onNext, this.onLoading)
        }
        else {
            if (feedText == "" || !feedText || String(feedText).startsWith('#') || String(feedText).startsWith('@')) {
                this.setState({ error: true })
                if (String(feedText).startsWith('#') || String(feedText).startsWith('@')) this.setState({ feedTextError: true })
                this.onLoading(false)
                return false
            }
            var obj = {
                wallId: localStorage.getItem('wallId'),
                networkId: selectedNetworkId,
                moderation: 0,
                account: 0,
                feedText: feedText,
                hashtagCaption: 0,
                filter: activeFilter ? activeFilter : contentObject.filters.length ? contentObject.filters[0].filter : null,
                accountId: '',
                hashtagIGTV: 0,
                onlyReels: 0,
            }
            if (selectedNetworkId == 3) {
                obj['field'] = feedText
                CREATE_OB_FEED_FB(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 23) {
                obj['airbnb_page_review'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 7) {
                obj['channelId'] = channelId
                obj['latlng'] = latlng;
                obj['keywords'] = keywords;
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 5) {
                obj['field1'] = feedText
                obj['field12boardurl'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 6) {
                obj['field2'] = feedText
                obj['field1'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 4) {
                obj['field33'] = feedText
                obj['field33placeId'] = placeId
                obj['field33placeName'] = placeName
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 28 && activeFilter == 70) {
                obj['tiktok_post_url'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 1) {
                obj['excludeRetweet'] = 1
                obj['excludeReplies'] = 1
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else {
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
        }
    }

    render() {
        const { contentObject, selectedNetworkId } = this.props
        const { filterName, error, activeFilter, feedText, keyword, isLoading, feedTextError, manualSelect } = this.state
        return <Tab.Container defaultActiveKey={contentObject.filters && contentObject.filters.length ? `feed-${contentObject.filters[0].filter}` : '0'}>
            {selectedNetworkId == 0 ? <>
                {manualSelect == null ? 
                    <ManualHead onBoarding={true} onBack={this.props.handleStep} onNext={this.props.onNext} setNetwork = {val=>this.setState({manualSelect: val})}/> 
                :
                    <ManualUploadWrap onBoarding={true} manualSelect={manualSelect} setNetwork={() => this.setState({manualSelect:null})} onNext={this.props.onNext}/>
                }

                </> 
                : 
                <div className='d-flex flex-column add_feed_ m-auto'>
                    <div className="d-flex align-items-center mb-5">
                        <div className="symbol symbol-40 flex-shrink-0 me-3">
                            <Image height={40} width={40} src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(selectedNetworkId)}.svg`)} alt={GET_NETWORK_NAME(selectedNetworkId)} fluid />
                        </div>
                        <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pr-3">
                            <span className="fs-6 fw-bold text-gray-800">{contentObject.name}</span>
                            <p className="fs-9 mb-0 fw-medium">Add your {contentObject.name} <span className=''>{filterName}</span></p>
                        </div>
                    </div>

                    <Nav variant="underline" className="mb-5 border-bottom">
                        {contentObject.filters && contentObject.filters.length > 0 ? contentObject.filters.map((filter, index) => (
                            <Nav.Item key={`feed-${filter.filter}`}>
                                <Nav.Link eventKey={`feed-${filter.filter}`} onClick={() => this.setState({ activeFilter: filter.filter, feedText: '', filterName: filter.feedtype, error: false, feedTextError: false })}>
                                    {filter.feedName}
                                </Nav.Link>
                            </Nav.Item>))
                            : null}
                        {SHOW_MORE_TAB(selectedNetworkId) ?
                            <Nav.Item>
                                <Nav.Link className="nav-link" onClick={() => this.onMoreTab()}>More</Nav.Link>
                            </Nav.Item>
                            : null}
                    </Nav>
                    <Tab.Content>
                        {selectedNetworkId == 29 ?
                            <>
                                <div className="mt-5 mb-10">
                                    <p>
                                        <strong>Share this URL to allow users to submit reviews on other marketing channels.</strong>
                                        <a href={`${HOST_URL}s/${localStorage.getItem('wallId')}/onsite-upload`} title='Qr'>{`${HOST_URL}s/${localStorage.getItem('wallId')}/onsite-upload`}</a>
                                    </p>
                                </div>
                            </>
                            :

                            contentObject.filters && contentObject.filters.length ? contentObject.filters.map((filter, index) => (
                                <Tab.Pane eventKey={`feed-${filter.filter}`}>
                                    {selectedNetworkId == 4 ?
                                        <Google onFieldTextChange={this.onFieldTextChange} error={error} />
                                        : selectedNetworkId == 3 ?
                                            <Facebook text={feedText} onTextChange={this.searchText} error={error} />
                                            : selectedNetworkId == 7 ?
                                                <Youtube tab={activeFilter} onValueChange={this.searchText} onChannelId={this.onChannelId} onLatlanChange={this.onLatlanChange} keyword={keyword} onKeywordChange={this.changeKeyword} error={error} feedText={feedText} />
                                                :
                                                <InputGroup className="mb-4">
                                                    <InputGroup.Text className='fw-semibold fs-5'><FontAwesomeIcon icon={getInputIcon(filter.filter, selectedNetworkId)} /></InputGroup.Text>
                                                    <Form.Control className={`fw-semibold ${error ? 'is-invalid' : ''}`} size='lg' placeholder={`Enter ${filter.feedtype}`} value={feedText} onChange={this.onInputFeedText} required />
                                                    {error ? <span className="invalid-feedback">{!feedTextError ? 'This field is required' : '@ and # not allowed'}</span> : null}
                                                </InputGroup>
                                    }
                                </Tab.Pane>
                            ))
                                : null}
                    </Tab.Content>

                    <Alert variant="warning" className='border border-warning'>
                        <Alert.Heading className='f-center alert_icon_head'>
                            <span className='f-center'>
                                <FontAwesomeIcon icon={'lightbulb'} iconClass={'me-1'} />
                                Pro Tip
                            </span>
                        </Alert.Heading>
                        <p className="fw-medium mb-0 fs-8 text-gray-700">We highly advise leveraging user-generated content to enhance your e-commerce brand.</p>
                    </Alert>
                    <div className="d-flex justify-content-between">
                        <Button variant='link' onClick={() => this.props.handleStep('c_n')}>
                            <FontAwesomeIcon icon={'arrow-left'} iconClass={'me-2'} />
                            Back
                        </Button>
                        <Button variant='primary' className={`${isLoading ? 'spinner' : ''}`} onClick={this.onAddFeed}>
                            Create feed
                        </Button>
                    </div>
                </div>}
        </Tab.Container>
    }
}

export default withRouter(AddContent)