import React, { Component } from 'react'
import { YOUTUBE_AUTOCOMPLETE } from '../../../../../../../actions/addFeedActions'
import PlacesAutocomplete, { geocodeByAddress, getLatLng } from 'react-places-autocomplete'
import { Form, InputGroup, Dropdown } from 'react-bootstrap'
import { FontAwesomeIcon } from '../../../../../../../Assets/AssetsHelper'
import { getInputIcon } from '../../../../Feeds/AddFeed/utils'
import ImageType from '../../../../CommonComponents/MediaType/ImageType'

export default class Youtube extends Component {
    state = { list: [], error: false, address:'', searchList:false }
    
    onYoutubeChange = query => {
        const { onValueChange, tab } = this.props
        if ([1, 75, 11].includes(tab)) {
            try {
                const type = tab == 11 ? "playlist" : "channel"
                onValueChange(query)
                YOUTUBE_AUTOCOMPLETE(query, type).then(res => {
                    const { data, status } = res
                    if (status == 200) {
                        this.setState({ list: data.items })
                    }
                })
            } catch (error) {
                console.log(error)
            }
        }
        else onValueChange(query)
    }

    onListClick = (item) => {
        try {
            const { onValueChange, tab, onChannelId, } = this.props
            onValueChange(tab == 11 ? item.snippet.title : item.snippet.channelTitle)
            onChannelId(`${tab == 11 ? item.id.playlistId : tab == 75 ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`)
            document.getElementById(`feedsfield-${tab}`).value = `https://www.youtube.com/${tab == 11 ? "playlist?list=" : "channel/"}${tab == 11 ? item.id.playlistId : tab == 75 ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`
            this.setState({ list: [] })
        } catch (error) {
            console.log("error------------------------", error)
        }
    }

    handleChange = (e) => { this.setState({address:e, searchList:true},() => this.props.onValueChange("",""))};

    handleSelect = address => {
        geocodeByAddress(address)
          .then(results => getLatLng(results[0]))
          .then(latLng => {
            this.props.onLatlanChange(`${latLng.lat},${latLng.lng}`)
          })
          .catch(error => console.error('Error', error));
    };

    setPlace = (v1, v2) => {
        this.props.onValueChange(v1, v2)
        this.setState({address:v1, searchList:false})
    };

    render() {
        const { tab, onValueChange, onLatlanChange, onKeywordChange, keyword, error, feedText } = this.props;
        const { list,  address, searchList,  } = this.state;
        return <div className="row">
            <div className={`col-md-${tab == 32 ? '6' : '12'}`}>
                <div className="form-group">
                    {tab == 32 ? <InputGroup className="mb-4">
                            <InputGroup.Text className='fw-semibold fs-5'><FontAwesomeIcon icon={getInputIcon(33, 7)}/></InputGroup.Text>
                            <Dropdown autoClose="outside">
                                <PlacesAutocomplete value={address} onChange={this.handleChange} onSelect={this.handleSelect}>
                                    {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => <>
                                        <Dropdown.Toggle variant="light" className={`arrow_disabled p-0 border-0 flex-grow-1 bg-transparent ${error ? 'is-invalid' : ''}`}>
                                        <Form.Control className={`fw-semibold ${error ? 'is-invalid' : ''}`} size='lg' {...getInputProps({ placeholder: "Type address" })} required/>
                                        {loading ? <div className='input-spinner'></div> : null}
                                        </Dropdown.Toggle>
                                        {list && address.length > 0 ?
                                                <Dropdown.Menu className='input-dropdown'>
                                                    <div className="dropdown-height">
                                                    {suggestions.map((suggestion ,i) => {
                                                            const style = {
                                                                backgroundColor: suggestion.active ? "" : ""
                                                            };
                                                            return <Dropdown.Item {...getSuggestionItemProps(suggestion, { style })} onClick={() => { this.setPlace(suggestion.description, suggestion.placeId); this.handleSelect(suggestion.description) }}>
                                                                <div className="d-flex flex-column justify-content-start">
                                                                    <p className="fw-bold fs-8 mb-0">{suggestion.description}</p>
                                                                </div>
                                                            </Dropdown.Item>
                                                        })}
                                                    </div>
                                            </Dropdown.Menu>
                                        : null}
                                    </>}
                                </PlacesAutocomplete>
                            </Dropdown>
                            {error ? <span className="invalid-feedback">This field is required</span> : null}
                        </InputGroup>
                        :
                        <InputGroup className="mb-4">
                            <InputGroup.Text className='fw-semibold fs-5'><FontAwesomeIcon icon={getInputIcon(tab == 75 || tab == 1 ? 4 : tab == 11 ? 11 : tab == 4 ? 4 : tab == 32 ? 33 : "", 7)}/></InputGroup.Text>
                            <PlacesAutocomplete onChange={this.onYoutubeChange} value={feedText} placeholder="test">
                            {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => <Dropdown>
                                    <Dropdown.Toggle variant="light" className={`arrow_disabled p-0 border-0 flex-grow-1 bg-transparent ${error ? 'is-invalid' : ''}`}>
                                        <Form.Control className={`fw-semibold ${error ? 'is-invalid' : ''}`} size='lg' {...getInputProps({ placeholder: `${tab == 32 ? "" : "Enter"} ${tab == 75 || tab == 1 ? "Channel Url" : tab == 11 ? "Play List ID" : tab == 4 ? "keyword" : ""}` })}/>
                                    </Dropdown.Toggle>
                                    {list && list.length > 0 ?
                                    <Dropdown.Menu className='input-dropdown'>
                                        <div className='dropdown-height'>
                                        {list.map((item ,i)=> (<Dropdown.Item className='d-flex align-items-center overflow-hidden w-100 rounded-0 border-bottom' data-item="place" {...getSuggestionItemProps(item)} key={i} onClick={() => this.onListClick(item)}> 
                                            <div className='d-flex flex-shrink-0 w-40px me-2'>
                                                <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 bg-gray-100'} size={100} height={40} width={40} url={item.snippet.thumbnails.default.url} isCover={true} alt={''}/>
                                            </div>
                                            <div className="d-flex flex-column justify-content-start" style={{maxWidth:'100%'}}>
                                                <p className="fw-bold fs-8 mb-0 text-ellpsis">{item.snippet.channelTitle}</p>
                                                <span className="fs-10 mb-0 text-ellpsis">{`https://www.youtube.com/${tab == 11 ? "playlist?list=" : "channel/"}${tab == 11 ? item.id.playlistId : item.id.channelId}`}</span>
                                            </div>
                                        </Dropdown.Item>))}
                                        </div>
                                    </Dropdown.Menu>
                                    : ''}
                                </Dropdown>
                            }
                            </PlacesAutocomplete>
                            {error ? <span className="invalid-feedback">This field is required</span> : null}
                        </InputGroup>   
                    }
                </div>
            </div>
            
            {tab == 32 ?
                <div className="col-md-6">
                    <div className="form-group">
                        <div className="input-group">
                            <InputGroup.Text className='fw-semibold fs-5'><FontAwesomeIcon icon={getInputIcon(4, 7)}/></InputGroup.Text>
                            <Form.Control placeholder="Enter keywords" size='lg' className='fw-semibold' value={keyword} onChange={e => onKeywordChange(e.target.value)}/>
                        </div>
                    </div>
                </div>
            : null}
        </div>
    }
}
