import React, { Component } from 'react';
import { Alert, Image, Button, Row, Col, Form } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import UploadBox from '../../CommonComponents/Uploads/UploadBox';
import moment from 'moment';
import { customPostsubmit } from '../../../../../actions/appsMoreAction';
import { DELETE_KEYS, getModeration } from '../../../../../actions/moderationActions';
import { TOAST_ERROR, TOAST_SUCCESS } from '../../../../../utils';
import { connect } from 'react-redux';
import withRouter from '../../../../routes/WithRouter';
import { TAGSHOP_USER_HOME_DATA } from '../../../../../actions/sceneActions';

class OnBoardingManualUpload extends Component {
       state = {
              postImage: '',
              mediaType: '',
              height: 0,
              width: 0,
              thumbnail: '',
              spinner: false,
              content: "",
              contentError: false
       }

       onChangePostImg = value => {
              this.setState({ postImage: value })
       }

       componentDidUpdate(prevProps) {
              const { editData } = this.props
              if (prevProps.editData != editData) {
                     this.setState({ postImage: editData.postFile, height: editData.mediaHeight, width: editData.mediaWidth, mediaType: editData.type })
              }
       }

       updateData = (data) => {
              if (data.type == "video") {
                     this.setState({ mediaType: data.type, height: data.height, width: data.width, postImage: data.s3Url, thumbnail: data.s3ThumbUrl })
              }
              else {
                     this.setState({ mediaType: data.type, height: data.meta.height, width: data.meta.width })
              }
       }

       uploadPost = (data) => {

              const { userData, onNext } = this.props
              const { content } = this.state
              const formData = new FormData();

              //formData.append('createdAt', moment(moment(new Date())).format('YYYY-MM-DD HH:mm'));
              //formData.append('link', '');
              //formData.append('feedId', 0);

              let type = 1;
              this.setState({ spinner: true });
              if (data.mediaType == 'content' || data.mediaType == 1) {
                     type = 1;
              } else if ((data.mediaType == 'image' || data.mediaType == 4) && content) {
                     type = 4;
              } else if ((data.mediaType == 'image' || data.mediaType == 2) && !content) {
                     type = 2;
              } else if ((data.mediaType == 'video' || data.mediaType == 3) && !content) {
                     type = 3;
              } else if ((data.mediaType == 'video' || data.mediaType == 5) && content) {
                     type = 5;
              }

              formData.append('postFile', data.mediaType == 'video' ? data.thumbnail : data.postImage);
              formData.append('mediaFile', data.mediaType == 'video' ? data.postImage : "");
              //formData.append('mediaFilePath', data.mediaType == 'video' ? data.postImage : "");

              // formData.append('authorFile', userData.picture);
              // formData.append('authorName', userData.name);
              formData.append('content', this.state.content);
              //formData.append('networkName', 'No Network');
              // formData.append('networkId', 0);
              //formData.append('recureEveryOption', 0);

              // formData.append('recureEvery', 0);
              // formData.append('recureAfter', 0);
              // formData.append('ownerId', userData.ownerId);
              //formData.append('referenceId', parseInt(Date.now()));
              formData.append('mediaHeight', data.height ? data.height : 0);
              formData.append('mediaWidth', data.width ? data.width : 0);
              formData.append('type', type);
              formData.append('wallId', localStorage.getItem('wallId'));
              // formData.append('postId', 0);
              // formData.append('rating', 0);
              // formData.append('isAdmin', 0);
              // formData.append('adminId', 0);
              // formData.append('videoType', 0);
              if (String(content).trim().length > 0 && String(content).trim().length <= 500) {
                     customPostsubmit(userData.id, localStorage.getItem('wallId'), formData).then((response) => {
                            if (response.data.responseCode == 200) {
                                   localStorage.setItem('selectedNetwork', null)
                                   if (response.data.responseStatus == 'success') {
                                          TOAST_SUCCESS(`Post Created Successfully.`);

                                          onNext()
                                   } else {
                                          TOAST_ERROR('Error');
                                   }
                            } else if (response.data.responseCode == 409) {
                                   TOAST_ERROR("Post Already Exists.");
                            }
                            this.setState({ spinner: false });
                     }).catch((error) => {
                            TOAST_ERROR(error.message);
                            this.setState({ spinner: false })

                     })
              }
              else this.setState({ spinner: false, contentError: true })

       }

       changeContent = (event) => this.setState({ content: (event.target.value).substring(0, 500), contentError: false })

       render() {
              const { navigate } = this.props
              const { postImage, mediaType, thumbnail, height, width, contentError, content, spinner } = this.state
              return (
                     <div className='d-flex flex-column add_feed_ m-auto' id="cont_modal">
                            <div className="d-flex align-items-center mb-5">
                                   <div className="symbol symbol-40 flex-shrink-0 me-3">
                                          <Image height={40} width={40} src={baseUrl(`media/icons/social-icons-square/manual-upload.svg`)} alt={''} fluid />
                                   </div>
                                   <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pr-3">
                                          <span className="fs-6 fw-bold text-gray-800">Manual Upload</span>
                                          <p className="fs-9 mb-0 fw-medium">Add Post</p>
                                   </div>
                            </div>
                            <div className='mb-5'>
                                   <Row>
                                          <Col md={6} lg={5} style={{ minHeight: 400 }}>
                                                 <UploadBox id="su_logo" backgroundImage={postImage} onClickTouploadImage={this.onChangePostImg} removeDelete={false} icon={'image-regular'} updateData={this.updateData} thumbnail={thumbnail} isEdit={true} isCover={true} />
                                          </Col>
                                          <Col md={6} lg={7}>
                                                 <Form.Group className="mb-2 mb-xl-3">
                                                        <Form.Label>Caption <span className="text-danger">*</span> </Form.Label>
                                                        <Form.Control as='textarea' id="mu_caption" maxLength={500} className={`${contentError ? "is-invalid" : ""} textarea-min-row-5`} name="caption" row="5" value={content} onChange={this.changeContent} />
                                                        {contentError ? <span className='invalid-feedback'>{String(content).length > 0 ? "Limit Exceed ! Max 500 characters allowed." : "This field is required."}</span> : null}
                                                        <div className="d-flex jsutify-content-end"> <span className="fs-10 text-muted pt-1">{content?.length}/500</span> </div>
                                                 </Form.Group>

                                          </Col>
                                   </Row>
                            </div>
                            <Alert variant="warning" className='border border-warning'>
                                   <Alert.Heading className='f-center alert_icon_head'>
                                          <span className='f-center'>
                                                 <FontAwesomeIcon icon={'lightbulb'} iconClass={'me-1'} />
                                                 Pro Tip
                                          </span>
                                   </Alert.Heading>
                                   <p className="fw-medium mb-0 fs-8 text-gray-700">We highly advise leveraging user-generated content to enhance your e-commerce brand.</p>
                            </Alert>
                            <div className="d-flex justify-content-between">
                                   <Button variant='link' id="mu_cancel_btn" onClick={() => this.props.onBack('c_n')}>
                                          <FontAwesomeIcon icon={'arrow-left'} iconClass={'me-2'} />
                                          Back
                                   </Button>
                                   <Button id='mu_sv_btn' className={`${spinner ? 'spinner' : ''}`} onClick={() => this.uploadPost(this.state)}>{"Upload"}</Button>
                            </div>
                     </div>
              );
       }
}

const mapStateToProps = (state) => {
       return {
              userData: state.authorized?.userData?.user_data?.user,
       }
}

const mapDispatchToProps = dispatch => {
       return {
              getModeration: (callback) => dispatch(getModeration(callback)),
              LoadHome: () => dispatch(TAGSHOP_USER_HOME_DATA()),
       }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(OnBoardingManualUpload));