import React, { useState } from "react";
import { Form, InputGroup, Dropdown } from "react-bootstrap";
import PlacesAutocomplete from "react-places-autocomplete";
import { FontAwesomeIcon } from "../../../../../../../Assets/AssetsHelper";
import { getInputIcon } from "../../../../Feeds/AddFeed/utils";

export default function Google(props) {
    const [address, setAddress] = useState("");
    const [list, setList] = useState(false);


    const handleChange = (e) => { setAddress(e); setList(true); props.onFieldTextChange("", "") };

    const handleSelect = (v1, v2, v3) => {
        props.onFieldTextChange(v1, v2, v3)
        setAddress(v1);
        setList(false)
    };

    return <InputGroup className="mb-4">
        <InputGroup.Text className='fw-semibold fs-5'><FontAwesomeIcon icon={getInputIcon(33, 4)} /></InputGroup.Text>
        <PlacesAutocomplete value={address} onChange={handleChange} >
            {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => {
                return <Dropdown autoClose="outside">
                    <Dropdown.Toggle variant="light" className={`arrow_disabled p-0 border-0 flex-grow-1 bg-transparent ${props.error ? 'is-invalid' : ''}`}>
                        <Form.Control className={`fw-semibold ${props.error ? 'is-invalid' : ''}`} size='lg' {...getInputProps({ placeholder: "Type address" })} required />
                        {loading ? <div className='input-spinner'></div> : null}
                    </Dropdown.Toggle>
                    {list && address.length > 0 ?
                        <Dropdown.Menu className="input-dropdown">
                            <div className="dropdown-height">
                                {suggestions.map((suggestion, i) => {
                                    const style = {
                                        backgroundColor: suggestion.active ? "" : ""
                                    };
                                    return <Dropdown.Item {...getSuggestionItemProps(suggestion, { style })} onClick={() => handleSelect(suggestion.description, suggestion.placeId, suggestion.formattedSuggestion.mainText)} key={i}>
                                        <div className="d-flex flex-column justify-content-start">
                                            <p className="fw-bold fs-8 mb-0">{suggestion.description}</p>
                                        </div>
                                    </Dropdown.Item>
                                })}
                            </div>
                        </Dropdown.Menu>
                        : null}
                </Dropdown>
            }}
        </PlacesAutocomplete>
        {props.error ? <span id="" className="invalid-feedback">This field is required</span> : null}
    </InputGroup>
}





