import React, { Component, useState } from 'react'
import { AUTOCOMPLETE } from '../../../../../../../actions/manageFeedsAction'
import { Form, InputGroup, Dropdown } from 'react-bootstrap'
import PlacesAutocomplete from 'react-places-autocomplete'
import { FontAwesomeIcon } from '../../../../../../../Assets/AssetsHelper'
import { getInputIcon } from '../../../../Feeds/AddFeed/utils'

export default function Facebook(props) {
    const [handles, setHandles] = useState([])
    const [showHandle, setShowHandle] = useState(false)

    const handleTextChange = e => {
        const value = e
        autoComplete(1, value)
        props.onTextChange(value)
        setShowHandle(true)
    }

    const autoComplete = (action, value) => {
        const obj = { networkId: 3, handle: value, filterType: 8, showHandle: false, }
        AUTOCOMPLETE(obj).then(res => {
            const { data } = res
            if (data.responseCode == 200) { setHandles(data.response.data) }
            else setHandles([])
        }).catch(ex => { })
    }

    const onSelectHandle = (item) => {
        props.onTextChange(item.link)
        document.querySelector(`#fb-text`).value = item.link
        setShowHandle(false)
        setHandles([])
    }

    return <InputGroup className="mb-4">
        <InputGroup.Text className='fw-semibold fs-5'><FontAwesomeIcon icon={getInputIcon(56, 3)} /></InputGroup.Text>
        <PlacesAutocomplete className="position-relative" value={props.text} onChange={handleTextChange} >
            {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (<>
                <Dropdown autoClose="outside" show={showHandle}>
                    <Dropdown.Toggle variant="light" className={`arrow_disabled p-0 border-0 flex-grow-1 bg-transparent ${props.error ? 'is-invalid' : ''}`}>
                        <Form.Control className={`fw-semibold ${props.error ? 'is-invalid' : ''}`} size='lg' {...getInputProps({ placeholder: "Enter Page Name" })} required id='fb-text' />
                        {loading ? <div className='input-spinner'></div> : null}
                    </Dropdown.Toggle>
                    {handles && handles.length > 0 ? 
                        <Dropdown.Menu className='input-dropdown'>
                            <div className="dropdown-height">
                                {handles.map((suggestion, i) => {
                                    return <Dropdown.Item className='rounded-0 border-bottom' onClick={() => onSelectHandle(suggestion)} key={i}>
                                        <div className="d-flex flex-column justify-content-start">
                                            <p className="fw-bold fs-8 mb-0">{suggestion.name}</p>
                                            <span className="fs-10 mb-0">{suggestion.link}</span>
                                        </div>
                                    </Dropdown.Item>
                                })}
                            </div>
                    </Dropdown.Menu>
                : null}
            </Dropdown></>)}

        </PlacesAutocomplete>
        {props.error ? <span id="" className="invalid-feedback">This field is required</span> : null}
    </InputGroup>

}

